/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Staatliches", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Staatliches", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-color: #B99163;
  padding: 3rem;
}
@media (min-width: 768px) {
  .header__contenido {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10rem;
  }
}

.nav__enlace {
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .nav__enlace {
    font-size: 2rem;
  }
}
.nav__enlace.activo::after {
  content: "";
  display: block;
  background-color: #FFFFFF;
  width: 8rem;
  height: 0.2rem;
  margin: 0 auto;
}

.seccionT {
  margin-top: 4rem;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .seccionT__grid {
    display: grid;
    gap: 2fr;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.seccionT__contenido {
  padding: 2rem;
}
.seccionT__titulo {
  font-size: 6rem;
  text-transform: uppercase;
  color: #E2B686;
  margin: 2rem 1rem;
}
.seccionT__cbtn {
  display: flex;
  justify-content: flex-end;
}
.seccionT__boton {
  background-color: #85C441;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .seccionT__boton {
    width: auto;
  }
}
.seccionT__boton:hover {
  background-color: #6ba032;
}

.seccionC {
  margin-top: 6rem;
  margin-bottom: 6rem;
}
@media (min-width: 768px) {
  .seccionC__grid {
    display: grid;
    gap: 5;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
  }
}
.seccionC__contenido {
  padding: 2rem;
}
@media (min-width: 768px) {
  .seccionC__contenido {
    grid-column: 2/4;
  }
}
.seccionC__img {
  overflow: hidden;
  order: -1;
}
@media (min-width: 768px) {
  .seccionC__img {
    grid-column: 1/2;
  }
}
.seccionC__titulo {
  font-size: 4rem;
  text-transform: uppercase;
  color: #E2B686;
}
.seccionC__texto {
  text-align: justify;
}

.intereses {
  margin-top: 6rem;
  margin-bottom: 6rem;
}
.intereses__titulo {
  font-size: 5rem;
  text-transform: uppercase;
  color: #E2B686;
  margin: 2rem auto;
}
@media (min-width: 480px) {
  .intereses__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  .intereses__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.interes__mask {
  overflow: hidden;
  height: 20rem;
}
.interes__img {
  cursor: pointer;
  transition: 0.3s ease-in-out all;
}
.interes__img img {
  height: 25rem;
}
.interes__img:hover {
  transform: scale(1.2);
}
.interes__cbtn {
  display: flex;
  justify-content: center;
}
.interes__nombre {
  text-align: center;
  text-transform: uppercase;
}
.interes__btn {
  background-color: #85C441;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .interes__btn {
    width: auto;
  }
}
.interes__btn:hover {
  background-color: #6ba032;
}

.footer {
  background-color: #E2B686;
}
.footer__grid {
  padding: 3rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 4 rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__mapa iframe {
  width: 100%;
}
.footer__redes nav a {
  display: block;
  color: #FFFFFF;
  margin-bottom: 2rem;
  font-weight: bold;
  text-align: center;
}
.footer__descripcion {
  margin-left: 2rem;
  margin-top: 3rem;
}
.footer__descripcion p {
  margin: 0;
  color: #FFFFFF;
}
.footer__copy {
  background-color: #694319;
}
.footer__copy p {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  padding: 1rem;
}

.planta {
  margin: 3rem auto;
}
@media (min-width: 768px) {
  .planta__grid {
    display: grid;
    gap: 6rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.planta p {
  text-align: justify;
}
.planta__titulo {
  color: #E2B686;
  text-transform: uppercase;
  text-align: center;
  margin: 1rem 0 3rem 0;
}
@media (min-width: 768px) {
  .planta__contenido {
    grid-column: 1/3;
  }
}
@media (min-width: 768px) {
  .planta__aside {
    grid-column: 3/4;
  }
}
.planta__subtitulos {
  color: #E2B686;
}
.planta__contenedor {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .planta__contenedor {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}